import React from 'react';
import ColorCircle from './ColorCircle';
import './ColorSelector.css'

function ColorSelector() {
    return (
        <div className='color-selector'>
            <ColorCircle r='255' g='0' b='0' />
            <ColorCircle r='255' g='127' b='0'/>
            <ColorCircle r='255' g='255' b='0'/>
            <ColorCircle r='127' g='255' b='0'/>
            <ColorCircle r='0' g='255' b='0'/>
            <ColorCircle r='0' g='255' b='127'/>
            <ColorCircle r='0' g='255' b='255'/>
            <ColorCircle r='0' g='127' b='255'/>
            <ColorCircle r='0' g='0' b='255'/>
            <ColorCircle r='127' g='0' b='255'/>
            <ColorCircle r='255' g='0' b='255'/>
            <ColorCircle r='255' g='0' b='127'/>
        </div>
    )
}

export default ColorSelector
