import React from 'react'
import axios from 'axios';
import Slider from '@material-ui/core/Slider';
import './RainbowTheater.css';

function RainbowTheater() {

    const handleSliderValue = (e) => {
        if (parseInt(e.target.textContent) > 0) {
            let reversedValue = ((parseInt(e.target.textContent) - 41) * (-1)) * 2;
            axios.post('http://192.168.178.48:1254/api/modus', {
                "modus": "rainbowTheater",
                "speed": reversedValue
            })
            .then(response => console.log(response))
            .catch(err => console.log(err))
        }
    }

    return (
        <div>
            <p>Rainbow-Theater</p>
            <div className="rainbowTheater-component">
                <div className="rainbowTheater-border">
                    <Slider
                        className="slider"
                        onChangeCommitted={handleSliderValue}
                        defaultValue={8}
                        valueLabelDisplay="auto"
                        step={1}
                        min={1}
                        max={20}
                    />
                </div>
            </div>
        </div>
    )
}

export default RainbowTheater
